import React, { Component } from 'react';

import {Switch, Route, Redirect} from 'react-router-dom'

import Header from './components/Header';
import HomePage from './components/HomePage';
import Space from './components/Space';
import FAQ from './components/FAQ';
import Prebuild from './components/Prebuild';
import Schedule from './components/Schedule';
import './App.css';

global.jQuery = require('jquery');

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lightboxIsOpen: false,
			language: 0,
    };
    this.openLightbox = this.openLightbox.bind(this);
    this.handleLanguage= this.handleLanguage.bind(this);
  }

  openLightbox(event) {
    this.setState({
      lightboxIsOpen: true,
      currentImage: event.currentTarget,
    });
  }

	handleLanguage(langValue) {
		this.setState({language: langValue});
	}

  render() {
    return (
      <div className="App">
        <Header onSelectLanguage={this.handleLanguage} />
          <div className="content-pages">
            <Switch>
              <Route exact path="/" render={() => <HomePage langValue={this.state.language}></HomePage>} />
              <Route path="/vision" render={() => <Space langValue={this.state.language}></Space>} />
              <Route path="/FAQ" render={() => <FAQ langValue={this.state.language}></FAQ>} />
              <Route path="/prebuild" render={() => <Prebuild langValue={this.state.language}></Prebuild>} />
              <Route path="/schedule" render={() => <Schedule langValue={this.state.language}></Schedule>} />
              <Redirect to="/" />
            </Switch>
        </div>
      </div>
    );
  }
}

export default App;



