import React, { Component } from 'react';
import mh from '../img/frontPage/mh.jpg';
import music from '../img/frontPage/music.jpg';
import shack from '../img/frontPage/shack.jpg';
import cobb1 from '../img/frontPage/cobb1.jpg';
import cobb2 from '../img/frontPage/cobb2.jpg';
import cobb3 from '../img/frontPage/cobb3.jpg';
import food from '../img/frontPage/food.jpg';
import axe from '../img/frontPage/axe.jpg';
import art from '../img/art-1.jpg';
// import  from '../img/.jpg';
import translations from '../translations.json';

class HomePage extends Component {
  render () {
		// <div className="background-field">
    return (
      <div>
        <hr></hr>
				<div>
        	<h1 className="title-text">{translations['homepage']['title'][this.props.langValue]}</h1>
        	<h4 className="title-text">{translations['homepage']['subtitle'][this.props.langValue]}</h4>
        	<h4 className="title-text pb-3">@ <a className="incognito-link" href="https://www.facebook.com/muellerhof19/">MUELLERHOF19, {translations['homepage']['dates'][this.props.langValue]} 14-23</a></h4>
				</div>
				<div className="row">
					<div className="col-12" dangerouslySetInnerHTML={{__html: translations['homepage']['intro'][this.props.langValue]}}></div>
					<div className="col-6">
          	<a href={music} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid pb-5" src={music} alt="" />
          	</a>
        	</div>
					<div className="col-6">
          	<a href={art} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid pb-5" src={art} alt="" />
          	</a>
          </div>
        </div>
				<div className="col-12" dangerouslySetInnerHTML={{__html: translations['homepage']['content1'][this.props.langValue]}}></div>
				<div className="row">
					<div className="col-4">
          	<a href={cobb1} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid pb-5" src={cobb1} alt="" />
          	</a>
        	</div>
					<div className="col-4">
          	<a href={cobb2} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid pb-5" src={cobb2} alt="" />
          	</a>
          </div>
					<div className="col-4">
          	<a href={cobb3} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid pb-5" src={cobb3} alt="" />
          	</a>
          </div>
        </div>
				<div className="col-12" dangerouslySetInnerHTML={{__html: translations['homepage']['content2'][this.props.langValue]}}></div>
				<div className="row">
					<div className="col-4">
          	<a href={shack} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid pb-5" src={shack} alt="" />
          	</a>
        	</div>
					<div className="col-4">
          	<a href={food} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid pb-5" src={food} alt="" />
          	</a>
          </div>
					<div className="col-4">
          	<a href={axe} data-lightbox="mhspace" data-title="">
          	  <img className="img-fluid pb-5" src={axe} alt="" />
          	</a>
          </div>
					<div className="col-12" dangerouslySetInnerHTML={{__html: translations['homepage']['donations'][this.props.langValue]}}></div>
        </div>
      </div>
    );
  }
}

export default HomePage;



