import React, { Component } from 'react';
import translations from '../translations.json';

class HomePage extends Component {
  render () {

    return (
      <div>
        <h1>{translations['prebuild']['title'][this.props.langValue]}</h1>
      </div>
    );
  }
}

export default HomePage;



