import React, { Component } from 'react';
import NavLink from "./NavLink";
import logo from '../img/logo.png';
import { Link } from 'react-router-dom';
import translations from '../translations.json';

class Header extends Component {
  constructor(props) {
    super(props);
		this.onLanguageClick= this.onLanguageClick.bind(this);
		this.state = {langValue: 0};
	}

	onLanguageClick(e) {
		const langValue = Number(e.target.dataset.language);
		this.setState({langValue: langValue});
		this.props.onSelectLanguage(langValue);
	}

	onHamburgerClick(e) {
		var menu = document.querySelector('#navbarSupportedContent');
		menu.classList.toggle('d-none');
	}
  
  render () {
    return (
			<nav className="navbar navbar-expand-md navbar-light nav-color sticky-top p-0 pt-2 menu-pad scrolled">
        <div className="d-flex">
          <Link to="/" className="logo-container">
			      <img src={logo} className="logo logo-margin" alt="logo" />
          </Link>
			    <button className="navbar-toggler hamburger mr-3" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation" onClick={this.onHamburgerClick}>
			      <span className="navbar-toggler-icon"></span>
			    </button>
        </div>
			
			  <div className="navbar-collapse collapse justify-content-end d-none container" id="navbarSupportedContent">
			    <ul className="navbar-nav">
            <NavLink to="/">
              <span>{translations['navbar']['home'][this.state.langValue]}</span>
            </NavLink>
            <NavLink to="/vision">
              <span>{translations['navbar']['space'][this.state.langValue]}</span>
            </NavLink>
            <NavLink to="/schedule">
              <span>{translations['navbar']['schedule'][this.state.langValue]}</span>
            </NavLink>
            <NavLink to="/prebuild" className="d-none">
              <span>{translations['navbar']['prebuild'][this.state.langValue]}</span>
            </NavLink>
						<li>
							<div className="divider-vertical"></div>
						</li>
            <li className="nav-item dropdown">
              <a className="nav-link dropdown-toggle pointer" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                {translations['navbar']['language'][this.state.langValue]}
              </a>
              <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                <a data-language="0" onClick={this.onLanguageClick} className="dropdown-item pointer">English</a>
                <a data-language="1" onClick={this.onLanguageClick} className="dropdown-item pointer">Deutsch</a>
                <a data-language="2" onClick={this.onLanguageClick} className="dropdown-item pointer">Espanol</a>
              </div>
            </li>
			    </ul>
			  </div>
			</nav>
    );
  }
}

export default Header;



